<template>
<f7-page>
  <f7-navbar>
    <f7-nav-left>
      <f7-link icon-if-ios="f7:menu" icon-if-md="material:menu" panel-open="left"></f7-link>
    </f7-nav-left>
    <f7-nav-title>紀念日</f7-nav-title>
  </f7-navbar>
  <f7-block strong>
    <p>記錄紀念日的小程式</p>
    <p>年用365天計算,月用30天計算</p>
  </f7-block>
  <f7-list>
    <!--<f7-list-item v-for="item in items" link="/dynamic-route/blog/45/post/125/?foo=bar#abo" :title="item.name" :footer="item.desc" :badge='item.key'></f7-list-item>-->
    <f7-list-item v-for="item in items" :title="item.name" :footer="item.desc" :badge='diff(item.then)'></f7-list-item>
  </f7-list>
  <f7-block-title>快速功能</f7-block-title>
  <f7-block strong>
    <f7-row>
      <f7-col width="50">
        <f7-button fill raised popup-open="#popup">新增下一露</f7-button>
      </f7-col>
    </f7-row>
  </f7-block>
</f7-page>
</template>
<script>
import moment from 'moment'
export default {
  methods: {
    diff(then) {

      var now = moment();
      var years = 0;
      var monthes = 0;
      var days = now.diff(moment(then), 'days');

      var result = "經過";
      years = Math.floor(days / 365);
      if (years > 0) {
        days = days % 365
        result += " " + years + " 年"
      }

      monthes = Math.floor(days / 30);
      if (monthes > 0) {
        days = days % 30
        result += " " + monthes + " 月"
      }

      result += " " + days + " 天"
      return result;
    }
  },
  data() {
    return {
      items: [{
        name: '爹的生日',
        then: '1952-08-15'
      },{
        name: '娘的生日',
        then: '1953-08-03'
      },{
        name: '我的生日',
        then: '1980-07-09'
      }, {
        name: '菓的生日',
        then: '1980-10-10'
      }, {
        name: '大學畢業',
        then: '2003-07-30'
      }, {
        name: '碩班畢業',
        then: '2005-07-30'
      }, {
        name: '結婚',
        then: '2008-06-15'
      }, {
        name: '漢的生日',
        then: '2009-01-02'
      }, {
        name: '中鋼上班',
        then: '2011-09-01'
      }, {
        name: '堡的生日',
        then: '2013-07-20'
      }]
    }
  }
}
</script>



